
import Scroller from 'vux/src/components/scroller/index.vue'

import {userAccount} from '@/service/getData'
export default {
  data() {
    return {
      list: [],
      userid: 230058
    };
  },
  components: {Scroller},
  async beforeMount() {
    let response = await userAccount(this.userid)
    this.list = response.data.book.item
    this.$nextTick(() => {
      this.$refs.scroller.reset()
    })
  },
  methods: {
    openTop(){
      this.$toast.top('top');
    },
    openCenter(){
      this.$toast.center('center');
    },
    openBottom(){
      this.$toast('bottom');  // or this.$toast.bottom('bottom');
    },
    openLoading(){
      this.$loading('loading...');
      let self = this;
      setTimeout(function () {
        self.closeLoading()
      }, 2000)
    },
    closeLoading(){
      this.$loading.close();
    }
  }
};



